@use 'base/mixins' as m;
@use 'base/variables' as v;

.pasos {
    margin-top: 5rem;

    &__wave {

    }
    &__contenido {
        background-color: v.$primario;
    }
    &__heading {
        text-align: center;
        color: v.$blanco;

        &--descripcion {
            font-size: 1.8rem;
            display: block;
        }
    }
    &__grid {
        @include m.tablet {
            @include m.grid(3, v.$separacion);
        }
    }
    &__imagen {
        transition: transform 300ms ease;
        &:hover {
            transform: rotate(-4deg)scale(1.1);
        }
    }
}
